.ModalBackground {
    background-color: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(20px);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    z-index: 0;
}